export default "import React from 'react';\nimport Bar from '@splunk/dashboard-visualizations/Bar';\n\nexport default () => (\n    <Bar\n        options={{\n            'axisLabelsY.integerUnits': true,\n        }}\n        encoding={{\n            x: 'primary.component',\n            y: 'primary.count',\n        }}\n        dataSources={{\n            primary: {\n                requestParams: { offset: 0, count: 20 },\n                data: {\n                    fields: [{ name: 'component', groupby_rank: '0' }, { name: 'count' }],\n                    columns: [\n                        ['ApplicationLicense', 'DatabaseDirectoryManager', 'IndexWriter'],\n                        ['2', '2.5', '3.5'],\n                    ],\n                },\n                meta: { totalCount: 20 },\n            },\n        }}\n    />\n);\n";